import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-analogclock',
  templateUrl: './analogclock.component.html',
  styleUrls: ['./analogclock.component.scss']
})
export class AnalogclockComponent implements OnInit {

  @ViewChild('hrHand') hrHand: ElementRef;
  @ViewChild('minHand') minHand: ElementRef;
  @ViewChild('secHand') secHand: ElementRef;

  constructor() { }

  ngOnInit() {
    setInterval(() => {
      const date = new Date();
      this.updateClock(date);
    }, 1000);
  }
  updateClock(date) {
    this.secHand.nativeElement.style.transform = 'rotate(' +
        date.getSeconds() * 6 + 'deg)';
    this.minHand.nativeElement.style.transform = 'rotate(' +
        date.getMinutes() * 6 + 'deg)';
    this.hrHand.nativeElement.style.transform = 'rotate(' +
        (date.getHours() * 30 + date.getMinutes() * 0.5) + 'deg)';
  }
}
